import React, { ReactElement, useState } from "react"
import { Box, BoxProps } from "../box"
import { InputField } from "./input-field"
import { SvgCalendar } from "../icons/calendar"
import { InputTypeEnum } from "../../core/enums"

type Props = BoxProps & {
  name: string
}

export const InputDate = ({ name, ...rest }: Props): ReactElement => {
  const [inputType, setInputType] = useState(InputTypeEnum.TEXT)

  return (
    <Box width="100%" position="relative">
      <InputField
        name={name}
        type={inputType}
        onFocus={(): void => setInputType(InputTypeEnum.DATE)}
        {...rest}
      />

      {inputType === InputTypeEnum.TEXT && (
        <SvgCalendar
          boxProps={{
            position: "absolute",
            top: "20px",
            right: "20px",
            cursor: "pointer",
            onClick: (): void => setInputType(InputTypeEnum.DATE),
          }}
        />
      )}
    </Box>
  )
}
