import React, { ReactElement } from "react"
import { t } from "../i18n/i18n"
import { withRouter, RouteComponentProps } from "react-router-dom"
import get from "lodash/get"
import { companiesTariffs } from "../fake-data"

import { Box } from "../ui/box"
import { HeaderPage } from "../ui/header.page"
import { TariffCards } from "../ui/tariff-cards"
import { Footer } from "../ui/footer/footer"
import { ResultsTitle } from "./ui/results-title"
import { HeaderFirstLine } from "./ui/header-first-line"
import { HeaderSecondLine } from "./ui/header-second-line"

export const CarLiabilityResultsPageView = ({
  history,
}: RouteComponentProps): ReactElement => {
  const state = get(history, "location.state") || {}
  const { purpose, type, seats, personalNumber, voluntaryAmount } = state

  const numberOfSeats = `${seats} ${t("carLiability.seatsNumber")}`

  const personalInsurance = personalNumber
    ? t("carLiability.results.personalInsurance", {
        0: personalNumber,
      })
    : ""
  const voluntaryInsurance = voluntaryAmount
    ? t("carLiability.results.voluntaryInsurance")
    : ""

  return (
    <Box>
      <HeaderPage />
      <ResultsTitle />

      <Box
        display="flex"
        flexDirection={["column", "column", "row"]}
        mb={["8px", "8px", "10px"]}
        p={[
          "16px 0 28px 37px",
          "16px 0 28px 37px",
          "15px 0 15px 37px",
          "15px 0 15px 185px",
        ]}
        bg="blues.4"
      >
        <HeaderFirstLine
          purpose={purpose}
          type={type}
          numberOfSeats={numberOfSeats}
        />

        {(personalInsurance || voluntaryInsurance) && (
          <HeaderSecondLine
            personalInsurance={personalInsurance}
            voluntaryInsurance={voluntaryInsurance}
          />
        )}
      </Box>

      <TariffCards tariffs={companiesTariffs} />

      <Footer mt={["97px", "97px", "100px"]} />
    </Box>
  )
}

export const CarLiabilityResultsPage = withRouter(CarLiabilityResultsPageView)
