import React, { ReactElement } from "react"
import { Box } from "../../ui/box"
import { SvgCar } from "../../ui/icons/car"
import { Text } from "../../ui/text"
import { t } from "../../i18n/i18n"

interface Props {
  purpose: string | undefined
  type: string | undefined
  numberOfSeats: string | undefined
}

const getDesktopPurpose = (purpose: string | undefined): string => {
  switch (purpose) {
    case "Personal":
      return "carLiability.results.personalPurpose"
    case "Commercial":
      return "carLiability.commercial"
    case "Trucks":
      return "carLiability.trucks"
  }

  return ""
}

export const HeaderFirstLine = ({
  purpose,
  type,
  numberOfSeats,
}: Props): ReactElement => (
  <Box display="flex">
    <SvgCar
      box={{ pt: "4px" }}
      props={{ style: { backgroundColor: "transparent" } }}
    />
    <Text
      display={["block", "block", "none"]}
      m="0 0 0 9px"
      fontSize={1}
      lineHeight="22px"
    >
      {purpose}
      <br />
      {`${type}, ${numberOfSeats}`}
    </Text>
    <Text
      display={["none", "none", "block"]}
      m="0 0 0 10px"
      fontSize={1}
      lineHeight="22px"
    >
      {`${t(getDesktopPurpose(purpose))} `}&nbsp;
      {` ${type}, ${numberOfSeats}`}
    </Text>
  </Box>
)
