import React, { ReactElement } from "react"
import { t } from "../../i18n/i18n"
import { SelectItem } from "../../core"

import { Box } from "../../ui/box"
import { Select } from "../../ui/select/select"

import { SecondStepSelectNote } from "./second-step-select-note"

interface Props {
  setFieldValue: (field: string, value: string | undefined) => void
}

const voluntaryAmountItems = [
  {
    id: "50",
    name: t("carLiability.step2.personalAmount", { 0: 50 }),
    value: "50",
  },
  {
    id: "80",
    name: t("carLiability.step2.personalAmount", { 0: 80 }),
    value: "80",
  },
  {
    id: "120",
    name: t("carLiability.step2.personalAmount", { 0: 120 }),
    value: "120",
  },
]

export const SecondStepSecondForm = ({
  setFieldValue,
}: Props): ReactElement => (
  <Box mt={["22px", "22px", "26px"]}>
    <Select
      name="voluntaryAmount"
      id="voluntary-amount-select"
      items={voluntaryAmountItems}
      inputProps={{
        fontSize: 3,
        placeholder: t("carLiability.step2.voluntaryAmountPlaceholder"),
      }}
      onChange={(item: SelectItem | null): void =>
        item ? setFieldValue("voluntaryAmount", item.value) : undefined
      }
    />

    <SecondStepSelectNote />
  </Box>
)
