import React, { ReactElement } from "react"
import { Formik } from "formik"
import { withRouter, RouteComponentProps } from "react-router-dom"
import { results } from "./routes"

import { Box } from "../ui/box"
import { SecondStepHeader } from "./ui/second-step-header"
import { SecondStepForm, Step2Values } from "./ui/second-step-form"

const initialValues = {
  personalAmount: undefined,
  personalNumber: undefined,
  voluntaryAmount: undefined,
}

export const CarLiabilitySecondStepPageView = ({
  history,
}: RouteComponentProps): ReactElement => {
  const onSubmit = (values: Step2Values) => {
    history.push({
      pathname: results,
      state: { ...history.location.state, ...values },
    })
  }

  return (
    <Box minHeight="100vh">
      <SecondStepHeader />

      <Formik
        initialValues={initialValues}
        onSubmit={onSubmit}
        component={SecondStepForm}
      />
    </Box>
  )
}

export const CarLiabilitySecondStepPage = withRouter(
  CarLiabilitySecondStepPageView,
)
