import React, { ReactElement, Fragment } from "react"
import { FormikProps } from "formik"

import { FormProps } from "./additional-insurances.form"
import { StepForm } from "../../ui/step-form"
import { StepFormCheckbox } from "./step-form-checkbox"
import { Box } from "../../ui/box"
import { useMediaQuery } from "react-responsive"
import { theme } from "../../theme/theme"
import { Select } from "../../ui/select/select"
import {
  purposeItems,
  seatsItems,
  typeItems,
  coverageItems,
} from "../../core/items"
import { t } from "../../i18n/i18n"
import { SelectItem } from "../../core"
import { CoverageSelectNote } from "./coverage-select-note"

const carLiabilityInsuranceFields: string[] = ["purpose", "type", "seats"]

export const AdditionalInsurancesView = ({
  values,
  setFieldValue,
}: FormikProps<FormProps>): ReactElement => {
  const isMobile = useMediaQuery({
    maxWidth: theme("breakpoints.1"),
  })

  return (
    <StepForm buttonBottom={["100px", "80px", "100px"]}>
      <Box
        display="flex"
        flexWrap="wrap"
        justifyContent={isMobile ? null : "space-between"}
        flexDirection={isMobile ? "column" : "row"}
        alignItems={isMobile ? "center" : null}
      >
        <Box maxWidth="345px" width={isMobile ? "100%" : "48%"}>
          <StepFormCheckbox
            isChecked={values.carLiabilityInsurance}
            onToggle={(isChecked: boolean) => {
              setFieldValue("carLiabilityInsurance", isChecked)
              carLiabilityInsuranceFields.forEach((field: string): void =>
                setFieldValue(field, ""),
              )
            }}
            mb={values.carLiabilityInsurance ? ["10px", "25px"] : "32px"}
          >
            {t("comprehensiveCar.carCivilLiabilityInsurance")}
          </StepFormCheckbox>
          {values.carLiabilityInsurance && (
            <Fragment>
              <Select
                id="purpose-select"
                name="purpose"
                items={purposeItems}
                inputProps={{
                  placeholder: t("carLiability.purpose"),
                  fontSize: 3,
                }}
                mt="15px"
                onChange={(selectedItem: SelectItem): void =>
                  setFieldValue("purpose", selectedItem.value)
                }
              />

              <Select
                name="type"
                id="type-select"
                items={typeItems}
                inputProps={{
                  placeholder: t("carLiability.type"),
                  fontSize: 3,
                }}
                mt="15px"
                onChange={(selectedItem: SelectItem): void =>
                  setFieldValue("type", selectedItem.value)
                }
              />

              <Select
                id="seats-select"
                name="seats"
                items={seatsItems}
                inputProps={{
                  placeholder: t("carLiability.seats"),
                  fontSize: 3,
                }}
                mt="15px"
                mb={["10px", "10px", "16px"]}
                onChange={(selectedItem: SelectItem): void =>
                  setFieldValue("seats", selectedItem.value)
                }
              />
            </Fragment>
          )}
        </Box>
        <Box maxWidth="345px" width={isMobile ? "100%" : "50%"}>
          <StepFormCheckbox
            isChecked={values.voluntaryLiabilityInsurance}
            onToggle={(isChecked: boolean) => {
              setFieldValue("voluntaryLiabilityInsurance", isChecked)
              setFieldValue("voluntaryLiabilityCoverage", "")
            }}
            mb={isMobile ? "15px" : "25px"}
          >
            {t("comprehensiveCar.voluntaryCivilLiabilityInsurance")}
          </StepFormCheckbox>
          {values.voluntaryLiabilityInsurance && (
            <Fragment>
              <Select
                id="coverage-select"
                name="voluntaryLiabilityCoverage"
                items={coverageItems}
                inputProps={{
                  placeholder: t("general.coverage"),
                  fontSize: 3,
                }}
                mt="15px"
                mb="0"
                onChange={(selectedItem: SelectItem): void =>
                  setFieldValue(
                    "voluntaryLiabilityCoverage",
                    selectedItem.value,
                  )
                }
              />
              <CoverageSelectNote htmlFor="voluntaryLiabilityCoverage" />
            </Fragment>
          )}
        </Box>
      </Box>
    </StepForm>
  )
}
